import React, { useState } from "react";
import { Link,useNavigate } from "react-router-dom";
import Logo from "./Common/Logo";
import axios from 'axios';

const Signup = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    phone: '',
    address: '',
    gender: '',
    role: '',
    bday: '',
    dp: null
  });
  const navigate = useNavigate();
  
  const handleChange = (event) => {
    const { name, value, type, files } = event.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: type === 'file' ? files[0] : value
    }));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const formDataWithPicture = new FormData();
      Object.entries(formData).forEach(([key, value]) => {
        formDataWithPicture.append(key, value);
      });
      const response = await axios.post('https://reactinfolabz.000webhostapp.com/API/signup.php', formDataWithPicture);
      navigate('/dashboard');
    } catch (error) {
      alert(error);
    }
  };

    return(
        <>
        <div className="container-xxl">
        <div className="authentication-wrapper authentication-basic container-p-y">
          <div className="authentication-inner">
            {/* Register Card */}
            <div className="card">
              <div className="card-body">
                {/* Logo */}
                <Logo />
                {/* /Logo */}
                <h4 className="mb-2" align="center">Create an account </h4>
                <p className="mb-4" align="center">Fill out the following details</p>

                <form
                  onSubmit={handleSubmit}
                  id="formAuthentication"
                  className="mb-3"
                  action="index.html"
                  method="POST"
                >
                  <div className="mb-3">
                    <label htmlFor="username" className="form-label">
                      Username
                    </label>
                    <input
                      type="text"
                      className="form-control"
                      id="username"
                      name="name"
                      placeholder="Enter your username"
                      autoFocus
                      value={formData.name}
                      onChange={handleChange}
                      required
                    />
                  </div>
                  <div className="mb-3">
                    <label htmlFor="email" className="form-label">
                      Email
                    </label>
                    <input
                      type="text"
                      value={formData.email}
                      onChange={handleChange}
                      required
                      className="form-control"
                      id="email"
                      name="email"
                      placeholder="Enter your email"
                    />
                  </div>
                  <div className="mb-3 form-password-toggle">
                    <label className="form-label" htmlFor="password">
                      Password
                    </label>
                    <div className="input-group input-group-merge">
                      <input
                        type="password"
                        id="password"
                        className="form-control"
                        name="password"
                        placeholder="&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;&#xb7;"
                        aria-describedby="password"
                        value={formData.password}
                        onChange={handleChange}
                        required
                      />
                      <span className="input-group-text cursor-pointer">
                        <i className="bx bx-hide"></i>
                      </span>
                    </div>
  </div>
  <div className="mb-3">
    <label htmlFor="phone" className="form-label">Phone</label>
    <input type="text" value={formData.phone} onChange={handleChange} required className="form-control" id="phone" name="phone" placeholder="Enter your phone number" />
  </div>
  <div className="mb-3">
    <label htmlFor="address" className="form-label">Address</label>
    <textarea value={formData.address} onChange={handleChange} required className="form-control" id="address" name="address" placeholder="Enter your address"></textarea>
  </div>
  <div className="mb-3">
    <label htmlFor="gender" className="form-label">Gender</label>
    <select value={formData.gender} onChange={handleChange} required className="form-control" id="gender" name="gender">
      <option value="">Select gender</option>
      <option value="male">Male</option>
      <option value="female">Female</option>
      <option value="other">Other</option>
    </select>
  </div>
  <div className="mb-3">
  <label htmlFor="role" className="form-label">
        Role:
        <select name="role" value={formData.role} onChange={handleChange} required className="form-control" id="role">
          <option value="">Select Role</option>
          <option value="1">Teacher</option>
          <option value="2">Student</option>
        </select>
      </label>
  </div>
  <div className="mb-3">
  <label htmlFor="bday" className="form-label">
        Birthday:
        <input type="date" name="bday" value={formData.bday} onChange={handleChange} required className="form-control" id="bday" />
      </label>
  </div>
  <div className="mb-3">
  <label htmlFor="dp" className="form-label">
        Profile Picture:
        <input type="file" name="dp" onChange={handleChange} required className="form-control" id="dp" />
      </label>
      <br />
  </div>
      <div className="mb-3">
          <div className="form-check">
            <input className="form-check-input" type="checkbox" id="terms-conditions" name="terms" /> Is all Information is correct??
          </div>
      </div>
          <button type="submit" className="btn btn-primary d-grid w-100">Sign up</button>
          
          <p className="text-center">
                <span>Already have an account?</span>
                <Link to="/">
                  <span>Sign in instead</span>
                </Link>
              </p>
    </form>

             
            </div>
          </div>
          {/* <!-- Register Card --> */}
        </div>
      </div>
    </div>        
        </>
    );
}
export default Signup;
// 