import React,{useState, useEffect} from "react";

export default function ContactDataTable(){
    return(
        <>    
       <div class="card">
                <h5 class="card-header">Messages</h5>
                <div class="table-responsive text-nowrap">
                  <table class="table">
                        <Header />
                        <tbody class="table-border-bottom-0">
                            <Data />
                            
                        </tbody>
                  </table>
                </div>
                </div>
                
        </>
    );
}

function Header(){
    return(
        <>
        <thead>
                      <tr>
                        <th>Sender Name</th>
                        <th>Sender Email</th>
                        <th>Sender Mobile No</th>
                        <th>Sender Message</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
        </>
    );
}

function Data(){
  const [data, setdata] = useState([]);
  const apiget = () => {
      fetch("https://business-analytics-dashboard.000webhostapp.com/ContactUs_SPA.php")
        .then((respone) => respone.json())
        .then((json) => {
          setdata(json.ContactUs);
        });
    };
    useEffect(() => {
      apiget();
    },[]);

    return(
        <>
        {data.map((value) => {
                return (
                  <>
        <tr>
                        <td>{value.Sender_Name}</td>
                        <td>{value.Sender_Mail}</td>
                        <td>
                        {value.Sender_Mobile}
                        </td>
                        <td><span>{value.Message}</span></td>
                        <td>
                          <div class="dropdown">
                            <button type="button" class="btn p-0 dropdown-toggle hide-arrow" data-bs-toggle="dropdown">
                              <i class="bx bx-dots-vertical-rounded"></i>
                            </button>
                            <div class="dropdown-menu">
                              
                              <a class="dropdown-item" href="javascript:void(0);"
                                ><i class="bx bx-trash me-1"></i> Delete</a>
                            </div>
                          </div>
                        </td>
                      </tr>
                      </>);})
                                        }
        </>
    );
}