import React, {useState, useEffect} from "react";

export default function OngoingOrderDataTable(){
    return(
        <>
            <div class="card">
                <h5 class="card-header">New Orders</h5>
                <div class="table-responsive text-nowrap">
                  <table class="table">
                    <thead>
                      <tr>
                        <th>Order ID</th>
                        <th>Client Name</th>
                        <th>Assigned To</th>
                        <th>Time</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody class="table-border-bottom-0">
                        <OngoingOrdersDataTable />
                    </tbody>
                  </table>
                </div>
            </div>
        </>
    );
}

function OngoingOrdersDataTable(){
  const [data, setdata] = useState([]);
  const apiget = () => {
      fetch("https://business-analytics-dashboard.000webhostapp.com/Ongoing_Orders_SPA.php")
        .then((respone) => respone.json())
        .then((json) => {
          setdata(json.Ongoing_Orders);
        });
    };
    useEffect(() => {
      apiget();
    },[]);
    return(
        <>
        {data.map((value) => {
                return (
                  <>
            
                    <tr>
                        <td><i class="fab fa-angular fa-lg text-danger me-3"></i> <strong>{value.Order_ID}</strong></td>
                        <td>
                          <ul class="list-unstyled users-list m-0 avatar-group d-flex align-items-center">
                            <li
                              data-bs-toggle="tooltip"
                              data-popup="tooltip-custom"
                              data-bs-placement="top"
                              class="avatar avatar-xs pull-up"
                              title="Lilian Fuller"
                            >
                              <img src="../assets/img/avatars/5.png" alt="Avatar" class="rounded-circle" />
                              {value.Client_Name}
                            </li>
                           
                          </ul>
                        </td>
                        <td>{value.AssignTo}</td>
                        <td><span class="badge bg-label-primary me-1">{value.Time}</span> </td>
                        
                        
                        <td>
                          <div class="dropdown">
                            <button type="button" class="btn p-0 dropdown-toggle hide-arrow" data-bs-toggle="dropdown">
                              <i class="bx bx-dots-vertical-rounded"></i>
                            </button>
                            <div class="dropdown-menu">
                              <a class="dropdown-item" href="javascript:void(0);"
                                ><i class="bx bx-edit-alt me-1"></i> Edit</a
                              >
                              <a class="dropdown-item" href="javascript:void(0);"
                                ><i class="bx bx-trash me-1"></i> Delete</a
                              >
                            </div>
                          </div>
                        </td>
                      </tr>
                      
                   
            </>);})
                                        }
        </>
    );
}